/**
 *   (Data Store Module) - Pinia Store
 *
 *  Pinia 
 * 
 *  API 
 * 
 *
 *   (Core Features):
 * 1.  
 *    - 
 *    - 
 *    - 
 * 2.  
 *    - 
 *    - 
 *    - 
 * 3.  
 *    - 
 *    - 
 *    -  D3.js 
 * 4.  
 *    - 
 *    - 
 *    - 
 *
 *   (Architecture Design):
 * -  store 
 * -  Vue 3  UI 
 * - 
 * -  TypeScript 
 * - 
 *
 *   (Technical Features):
 * - Pinia  Vue 3 
 * - Composition API Vue 3  Composition API 
 * -  Promise  async/await 
 * - 
 * - 
 *
 *   (State Structure):
 * - layers: 
 * - layerStates: 
 * - selectedFeature: 
 * - d3jsDimensions: D3.js 
 *
 *   (Usage Examples):
 * ```javascript
 * //  store
 * import { useDataStore } from '@/stores/dataStore';
 *
 * const dataStore = useDataStore();
 *
 * // 
 * await dataStore.toggleLayerVisibility('taipei_metro');
 *
 * // 
 * const visibleLayers = dataStore.visibleLayers;
 *
 * // 
 * dataStore.setSelectedFeature(feature);
 * ```
 *
 *   (Data Flow):
 * 1.  store 
 * 2. Store 
 * 3.  UI 
 * 4. 
 * 5. 
 *
 *   (Important Notes):
 * -  store 
 * - 
 * - 
 * - 
 *
 * @file dataStore.js
 * @version 3.0.0
 * @author Kevin Cheng
 * @since 1.0.0
 * @see {@link https://pinia.vuejs.org/} Pinia 
 * @see {@link https://vuejs.org/guide/composition-api/} Vue 3 Composition API 
 */
// ====================   (Third-Party Library Imports) ====================

/**
 * Pinia 
 * Vue 3 
 *  TypeScript 
 *
 * @see https://pinia.vuejs.org/
 */
import { defineStore } from 'pinia';

/**
 * Vue 3 Composition API 
 * 
 *
 * @see https://vuejs.org/
 */
import { ref, computed } from 'vue';

/**
 * 
 * 
 */
import { loadDataLayerJson, loadGridSchematicJson } from '../utils/dataProcessor.js';

// ====================   (Main Data Store Definition) ====================

/**
 *   Store  (Data Store Instance)
 *
 *  Pinia  defineStore  Composition API 
 *  store 
 *
 *  Store  (Store Features):
 * - 
 * - 
 * - 
 * - 
 * -  D3.js 
 *
 *   (Technical Implementation):
 * -  defineStore  Pinia store
 * -  Composition API 
 * - persist: true
 * - 
 *
 * @type {Store} Pinia Store 
 * @since 1.0.0
 */
export const useDataStore = defineStore(
  'data',
  () => {
    // ====================   (Layer State Management) ====================

    /**
     *   (Layer States Storage)
     *
     * 
     *  UI 
     *
     * 
     * - layerId: 
     * - visible: 
     * - isLoading: 
     * - isLoaded: 
     * - jsonData:  JSON 
     * - processedJsonData:  JSON 
     * - dashboardData: 
     * - dataTableData: 
     * - layerInfoData: 
     *
     * @type {Ref<Object>} 
     * @since 1.0.0
     */
    const layerStates = ref({});

    /**
     *   (Layer Configuration Definition)
     *
     * 
     * 
     *
     * 
     * - 
     * - 
     *
     * 
     * - layerId: 
     * - layerName: 
     * - visible: 
     * - isLoading: 
     * - isLoaded: 
     * - type: grid, point, line 
     * - shape:  null
     * - colorName: 
     * - jsonData:  JSON 
     * - processedJsonData:  JSON 
     * - dashboardData: 
     * - dataTableData: 
     * - layerInfoData: 
     * - jsonLoader: 
     * - jsonFileName:  JSON 
     * - isDataLayer: 
     * - hideFromMap: 
     * - display: 
     * - isGridSchematic: 
     *
     * @type {Ref<Array>} 
     * @since 1.0.0
     */
    const layers = ref([
      {
        groupName: '',
        groupLayers: [
          {
            layerId: 'test_layer',
            layerName: '',
            visible: false,
            isLoading: false,
            isLoaded: false,
            type: 'grid',
            shape: null,
            colorName: 'green',
            jsonData: null,
            processedJsonData: null,
            dashboardData: null,
            dataTableData: null,
            layerInfoData: null,
            jsonLoader: loadGridSchematicJson,
            jsonFileName: 'test/test.json',
            isDataLayer: true,
            hideFromMap: true,
            display: true,
            isGridSchematic: true, // 
          },
        ],
      },
      {
        groupName: '',
        groupLayers: [
          {
            layerId: 'taipei_metro',
            layerName: 'Taipei Metro',
            visible: false,
            isLoading: false,
            isLoaded: false,
            type: 'point',
            shape: null,
            colorName: 'orange',
            jsonData: null,
            processedJsonData: null,
            dashboardData: null,
            dataTableData: null,
            layerInfoData: null,
            jsonLoader: loadDataLayerJson,
            jsonFileName: 'taipei/taipei_schematic.json',
            isDataLayer: true,
            hideFromMap: true,
            display: true,
          },
          {
            layerId: 'taipei_metro_2',
            layerName: 'Taipei Metro 2',
            visible: false,
            isLoading: false,
            isLoaded: false,
            type: 'point',
            shape: null,
            colorName: 'orange',
            jsonData: null,
            processedJsonData: null,
            dashboardData: null,
            dataTableData: null,
            layerInfoData: null,
            jsonLoader: loadDataLayerJson,
            jsonFileName: 'taipei/taipei_schematic_2.json',
            isDataLayer: true,
            hideFromMap: true,
            display: true,
          },
        ],
      },
    ]);

    /**
     *   (Save Layer State)
     *
     *  layerStates 
     * 
     *
     *   (Main Features):
     * - 
     * - 
     * -  Object.assign 
     * -  Vue 
     *
     *   (Technical Implementation):
     * - 
     * -  Object.assign 
     * - 
     *
     *   (Usage Examples):
     * ```javascript
     * // 
     * saveLayerState('taipei_metro', { visible: true });
     *
     * // 
     * saveLayerState('taipei_metro', { isLoading: true });
     *
     * // 
     * saveLayerState('taipei_metro', {
     *   jsonData: data,
     *   dashboardData: summary,
     *   dataTableData: table,
     *   layerInfoData: info
     * });
     * ```
     *
     * @param {string} layerId - 
     * @param {Object} stateData - 
     * @param {boolean} [stateData.visible] - 
     * @param {boolean} [stateData.isLoading] - 
     * @param {boolean} [stateData.isLoaded] - 
     * @param {Object} [stateData.jsonData] -  JSON 
     * @param {Object} [stateData.processedJsonData] -  JSON 
     * @param {Object} [stateData.dashboardData] - 
     * @param {Array} [stateData.dataTableData] - 
     * @param {Object} [stateData.layerInfoData] - 
     *
     * @example
     * // 
     * saveLayerState('test_layer', { visible: true });
     *
     * @since 1.0.0
     */
    const saveLayerState = (layerId, stateData) => {
      if (!layerStates.value[layerId]) {
        layerStates.value[layerId] = {};
      }
      Object.assign(layerStates.value[layerId], stateData);
    };

    // ====================   (Layer Search Functions) ====================

    /**
     *   ID  (Find Layer by ID)
     *
     *  ID 
     * 
     *
     *   (Main Features):
     * -  layerId 
     * - 
     * - 
     * -  null
     *
     *   (Technical Implementation):
     * - 
     * - === ID 
     * - 
     * -  null 
     *
     *   (Usage Examples):
     * ```javascript
     * // 
     * const layer = findLayerById('taipei_metro');
     * if (layer) {
     *   console.log(':', layer.layerName);
     * } else {
     *   console.log('');
     * }
     *
     * // 
     * if (findLayerById('test_layer')) {
     *   console.log('');
     * }
     * ```
     *
     *   (Search Scope):
     * - 
     * - 
     * - 
     *
     *   (Important Notes):
     * - 
     * - 
     * -  null
     *
     * @param {string} layerId -  ID
     * @returns {Object|null} -  null
     *
     * @example
     * // 
     * const layer = findLayerById('taipei_metro');
     * if (layer) {
     *   console.log(layer.layerName);
     * }
     *
     * @since 1.0.0
     */
    const findLayerById = (layerId) => {
      for (const mainGroup of layers.value) {
        for (const layer of mainGroup.groupLayers) {
          if (layer.layerId === layerId) {
            return layer;
          }
        }
      }
      return null;
    };

    /**
     *   (Get All Layers)
     *
     * 
     * 
     *
     *   (Main Features):
     * - 
     * - 
     * - 
     * - 
     *
     *   (Technical Implementation):
     * -  for...of 
     * - ...
     * - 
     * - 
     *
     *   (Usage Examples):
     * ```javascript
     * // 
     * const allLayers = getAllLayers();
     * console.log(':', allLayers.length);
     *
     * // 
     * const visibleLayers = allLayers.filter(layer => layer.visible);
     *
     * // 
     * const loadingLayers = allLayers.filter(layer => layer.isLoading);
     *
     * // 
     * const gridLayers = allLayers.filter(layer => layer.type === 'grid');
     * ```
     *
     *   (Return Data Structure):
     * ```javascript
     * [
     *   {
     *     layerId: string,        // 
     *     layerName: string,      // 
     *     visible: boolean,       // 
     *     isLoading: boolean,     // 
     *     isLoaded: boolean,      // 
     *     type: string,           // 
     *     colorName: string,      // 
     *     jsonData: Object,       //  JSON 
     *     processedJsonData: Object, //  JSON 
     *     dashboardData: Object,    // 
     *     dataTableData: Array,       // 
     *     layerInfoData: Object,      // 
     *     jsonLoader: Function,   // 
     *     jsonFileName: string,   //  JSON 
     *     isDataLayer: boolean,   // 
     *     hideFromMap: boolean,   // 
     *     display: boolean,       // 
     *     isGridSchematic: boolean // 
     *   }
     * ]
     * ```
     *
     *   (Use Cases):
     * - 
     * - 
     * - 
     * - 
     *
     *   (Important Notes):
     * - 
     * - 
     * - 
     *
     * @returns {Array} - 
     *
     * @example
     * // 
     * const allLayers = getAllLayers();
     * console.log(':', allLayers.length);
     *
     * @since 1.0.0
     */
    const getAllLayers = () => {
      const allLayers = [];
      for (const mainGroup of layers.value) {
        allLayers.push(...mainGroup.groupLayers);
      }
      return allLayers;
    };

    // ====================   (Main Layer Processing Functions) ====================

    /**
     *   (Toggle Layer Visibility)
     *
     * /
     * 
     *
     *   (Main Features):
     * - /
     * - 
     * - 
     * - 
     * - 
     *
     *   (Technical Implementation):
     * -  async/await 
     * - 
     * - 
     * - 
     * - 
     *
     *   (Operation Flow):
     * 1.  layerId 
     * 2. 
     * 3. 
     * 4. 
     * 5. 
     * 6. 
     *
     *   (Usage Examples):
     * ```javascript
     * // 
     * await toggleLayerVisibility('taipei_metro');
     *
     * // 
     * await toggleLayerVisibility('taipei_metro');
     *
     * // 
     * const handleLayerToggle = async (layerId) => {
     *   try {
     *     await toggleLayerVisibility(layerId);
     *     console.log('');
     *   } catch (error) {
     *     console.error(':', error);
     *   }
     * };
     * ```
     *
     *   (Loading Conditions):
     * - 
     * - visible: true
     * - isLoaded: false
     * - isLoading: false
     *
     *   (Error Handling):
     * - 
     * - 
     * - 
     * - 
     *
     *   (Debug Information):
     * - 
     * - 
     * - 
     * - 
     *
     * @param {string} layerId -  ID
     * @returns {Promise<void>} - 
     * @throws {Error} - 
     *
     * @example
     * // 
     * await toggleLayerVisibility('test_layer');
     *
     * @since 1.0.0
     * @see {@link findLayerById} 
     * @see {@link saveLayerState} 
     */
    const toggleLayerVisibility = async (layerId) => {
      const layer = findLayerById(layerId);
      if (!layer) {
        console.error(` DataStore: Layer with id "${layerId}" not found.`);
        return;
      }

      console.log(' DataStore: ', {
        layerId,
        layerName: layer.layerName,
        currentVisible: layer.visible,
        isLoaded: layer.isLoaded,
        isLoading: layer.isLoading,
      });

      // 
      layer.visible = !layer.visible;

      // 
      saveLayerState(layerId, { visible: layer.visible });

      // 
      const shouldLoad = layer.visible && !layer.isLoaded && !layer.isLoading;
      console.log(' DataStore: ', {
        visible: layer.visible,
        isLoaded: layer.isLoaded,
        isLoading: layer.isLoading,
        shouldLoad,
      });

      if (shouldLoad) {
        try {
          layer.isLoading = true;
          saveLayerState(layerId, { isLoading: layer.isLoading });

          // 
          const result = await layer.jsonLoader(layer);

          // 
          layer.jsonData = result.jsonData;
          layer.processedJsonData = result.processedJsonData;
          layer.dataTableData = result.dataTableData;
          layer.dashboardData = result.dashboardData;
          layer.layerInfoData = result.layerInfoData;
          layer.isLoaded = true;

          console.log(`  "${layer.layerName}" `);
          console.log(` :`, layer.dashboardData);

          // 
          saveLayerState(layerId, {
            isLoaded: layer.isLoaded,
            jsonData: layer.jsonData,
            processedJsonData: layer.processedJsonData,
            dataTableData: layer.dataTableData,
            dashboardData: layer.dashboardData,
            layerInfoData: layer.layerInfoData,
          });
        } catch (error) {
          console.error(`  "${layer.layerName}" :`, error);
          layer.visible = false; // 
          saveLayerState(layerId, { visible: false });
        } finally {
          layer.isLoading = false;
          saveLayerState(layerId, { isLoading: false });
        }
      }
    };

    // ====================  ====================

    // 
    const selectedFeature = ref(null);

    // ==================== D3jsTab  ====================

    // D3jsTab 
    const d3jsDimensions = ref({
      width: 0,
      height: 0,
    });

    //  D3jsTab 
    const updateD3jsDimensions = (width, height) => {
      d3jsDimensions.value = {
        width: Math.round(width),
        height: Math.round(height),
      };
      console.log(' D3jsTab :', d3jsDimensions.value);
    };

    const setSelectedFeature = (feature) => {
      // log
      if (feature) {
        console.log(' DataStore: :', feature.properties?.id);
      } else {
        console.log(' DataStore: ');
      }
      selectedFeature.value = feature;
    };

    const clearSelectedFeature = () => {
      console.log(' DataStore: ');
      selectedFeature.value = null;
    };

    /**
     * ID
     * @param {string} layerId - ID
     * @returns {string|null} - null
     */
    const findGroupNameByLayerId = (layerId) => {
      for (const mainGroup of layers.value) {
        for (const layer of mainGroup.groupLayers) {
          if (layer.layerId === layerId) {
            return mainGroup.groupName;
          }
        }
      }
      return null;
    };

    // ====================  ====================

    return {
      layers,
      findLayerById, //  ID 
      getAllLayers, // 
      findGroupNameByLayerId, // ID
      toggleLayerVisibility,
      selectedFeature,
      setSelectedFeature,
      clearSelectedFeature,
      visibleLayers: computed(() => getAllLayers().filter((layer) => layer.visible)),
      loadingLayers: computed(() => getAllLayers().filter((layer) => layer.isLoading)),
      // 
      layerStates,
      saveLayerState,
      // D3jsTab 
      d3jsDimensions,
      updateD3jsDimensions,
    };
  },
  {
    persist: true,
  }
);
