/**
 *   (Generic File Loading Function)
 *
 * 
 * 
 * 
 *
 *   (Core Features):
 * - 
 * - 
 * - 
 * - 
 * - 
 *
 *   (Technical Implementation):
 * -  Fetch API 
 * -  HTTP 
 * - 
 * -  Promise  async/await 
 *
 *   (Loading Process):
 * 1. 
 * 2. 
 * 3.  HTTP 
 * 4. 
 * 5. 
 * 6. 
 *
 *   (Usage Examples):
 * ```javascript
 * //  - 
 * try {
 *   const response = await loadFile('/data/example.json');
 *   const data = await response.json();
 *   console.log(':', data);
 * } catch (error) {
 *   console.error(':', error.message);
 * }
 *
 * //  - 
 * try {
 *   const response = await loadFile(
 *     '/schematic-map-rwd/data/taipei.json',
 *     '/data/taipei.json'
 *   );
 *   const data = await response.json();
 *   console.log(':', data);
 * } catch (error) {
 *   console.error(':', error.message);
 * }
 * ```
 *
 *   (Error Handling):
 * - 
 * - HTTP 4xx  5xx 
 * - 404 
 * - 403 
 * - 
 *
 *   (Debug Information):
 * - 
 * - 
 * - 
 * - 
 *
 * @param {string} primaryPath - 
 * @param {string} [fallbackPath=null] - 
 * @returns {Promise<Response>} - 
 * @throws {Error} - 
 *
 * @example
 * //  JSON 
 * const response = await loadFile('/data/example.json');
 * const data = await response.json();
 *
 * // 
 * const response = await loadFile('/data/file.json', '/fallback/file.json');
 *
 * @since 1.0.0
 * @see {@link https://developer.mozilla.org/en-US/docs/Web/API/Fetch_API} Fetch API 
 * @see {@link https://developer.mozilla.org/en-US/docs/Web/API/Response} Response 
 */
async function loadFile(primaryPath, fallbackPath = null) {
  console.log(` : ${primaryPath}`);

  try {
    // 
    const response = await fetch(primaryPath);

    if (!response.ok) {
      throw new Error(`HTTP ${response.status}: ${response.statusText}`);
    }

    console.log(` : ${primaryPath}`);
    return response;
  } catch (error) {
    console.warn(` : ${primaryPath}`, error.message);

    // 
    if (fallbackPath) {
      console.log(` : ${fallbackPath}`);

      try {
        const fallbackResponse = await fetch(fallbackPath);

        if (!fallbackResponse.ok) {
          throw new Error(`HTTP ${fallbackResponse.status}: ${fallbackResponse.statusText}`);
        }

        console.log(` : ${fallbackPath}`);
        return fallbackResponse;
      } catch (fallbackError) {
        console.error(` : ${fallbackPath}`, fallbackError.message);
        throw new Error(
          `: ${primaryPath}, : ${fallbackPath}: ${error.message}`
        );
      }
    }

    // 
    throw new Error(`: ${primaryPath}: ${error.message}`);
  }
}

