/**
 *   JSON  (Load Data Layer JSON Data)
 *
 *  JSON 
 * 
 * 
 *
 *   (Main Features):
 * -  JSON 
 * - 
 * - 
 * - 
 * - 
 *
 *   (Supported Data Formats):
 * - 
 * -  JSON 
 * - 
 *
 *   (Data Processing Flow):
 * 1.  JSON 
 * 2.  loadFile  JSON 
 * 3.  JSON 
 * 4.  processDataLayerJson 
 * 5. 
 *
 *   (Usage Examples):
 * ```javascript
 * // 
 * const layer = {
 *   jsonFileName: 'taipei/taipei_schematic.json',
 *   layerName: 'Taipei Metro',
 *   type: 'point'
 * };
 *
 * try {
 *   const result = await loadDataLayerJson(layer);
 *   console.log(':', result.dashboardData);
 *   console.log(':', result.dataTableData);
 * } catch (error) {
 *   console.error(':', error.message);
 * }
 * ```
 *
 *   (Return Data Structure):
 * ```javascript
 * {
 *   jsonData: Object | null,     //  JSON 
 *   processedJsonData: Object | null, //  JSON 
 *   dashboardData: Object,         // 
 *   dataTableData: Array,           // 
 *   layerInfoData: Object,          // 
 * }
 * ```
 *
 *   (Important Notes):
 * -  await  .then() 
 * - 
 * - 
 * - 
 *
 * @param {Object} layer - 
 * @param {string} layer.jsonFileName - JSON 
 * @param {string} [layer.layerName] - 
 * @param {string} [layer.type] - 
 * @returns {Promise<Object>} - 
 * @throws {Error} - 
 *
 * @example
 * // 
 * const layer = { jsonFileName: 'taipei/metro.json' };
 * const data = await loadDataLayerJson(layer);
 *
 * @since 1.0.0
 * @see {@link loadFile} 
 * @see {@link processDataLayerJson} 
 */
export async function loadDataLayerJson(layer) {
  try {
    console.log('  JSON ...');

    const fileName = layer.jsonFileName;
    //  /data/ 
    const dataPath = `${PATH_CONFIG.JSON}/${fileName}`;
    const response = await loadFile(dataPath);

    const jsonData = await response.json();

    // 
    return await processDataLayerJson(jsonData);
  } catch (error) {
    console.error('  JSON :', error);
    throw error;
  }
}

/**
 *   (Randomize Node Values)
 *
 * 
 *  1-5 
 * 
 *
 *   (Main Features):
 * -  1-5 
 * - 
 * - 
 * - 
 *
 *   (Technical Implementation):
 * -  Math.random() 
 * -  Math.floor() 
 * -  map() 
 * - 
 *
 *   (Value Distribution):
 * - 1
 * - 5
 * - 
 * - 
 *
 *   (Usage Examples):
 * ```javascript
 * // 
 * const nodes = [
 *   { id: 1, coord: { x: 0, y: 0 }, value: 0 },
 *   { id: 2, coord: { x: 1, y: 0 }, value: 0 }
 * ];
 *
 * const randomizedNodes = randomizeNodeValues(nodes);
 * console.log(randomizedNodes);
 * // : [
 * //   { id: 1, coord: { x: 0, y: 0 }, value: 3 },
 * //   { id: 2, coord: { x: 1, y: 0 }, value: 1 }
 * // ]
 * ```
 *
 *   (Input Data Format):
 * ```javascript
 * [
 *   {
 *     id: number,           // 
 *     coord: { x: number, y: number },  // 
 *     value: any,           // 
 *     []: any       // 
 *   }
 * ]
 * ```
 *
 *   (Output Data Format):
 * ```javascript
 * [
 *   {
 *     id: number,           // 
 *     coord: { x: number, y: number },  // 
 *     value: number,        // 1-5
 *     []: any       // 
 *   }
 * ]
 * ```
 *
 *   (Debug Information):
 * - 
 * - 
 * - 
 *
 *   (Important Notes):
 * -  value 
 * - 
 * - 
 * - 
 *
 * @param {Array} nodes - 
 * @returns {Array} -  value  1-5 
 *
 * @example
 * // 
 * const nodes = [{ id: 1, coord: { x: 0, y: 0 }, value: 0 }];
 * const result = randomizeNodeValues(nodes);
 *
 * @since 1.0.0
 * @see {@link https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Math/random} Math.random() 
 * @see {@link https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/map} Array.map() 
 */
function randomizeNodeValues(nodes) {
  console.log(' :', nodes.length);
  const randomizedNodes = nodes.map((node) => {
    const newValue = Math.floor(Math.random() * 5) + 1; //  1-5 
    console.log(`  ${node.coord?.x},${node.coord?.y}  ${node.value}  ${newValue}`);
    return {
      ...node,
      value: newValue,
    };
  });
  console.log(' 3:', randomizedNodes.slice(0, 3));
  return randomizedNodes;
}

