/**
 *   JSON  (Process Grid Schematic JSON Data)
 *
 *  JSON 
 *  D3.js 
 * 
 *
 *   (Main Features):
 * -  JSON  x, y 
 * - 
 * - 
 * - 
 * - 
 *
 *   (Technical Implementation):
 * - 
 * -  1-5 
 * -  x, y 
 * - 
 *
 *   (Grid Generation Logic):
 * -  Y 
 * -  X 
 * - 
 * -  Math.random()  1-5 
 * -  1
 *
 *   (Usage Examples):
 * ```javascript
 * // 
 * const jsonData = { x: 5, y: 5 };
 * const result = await processGridSchematicJson(jsonData);
 *
 * console.log(':', result.jsonData.nodes.length);
 * console.log(':', result.dashboardData.gridSize);
 * console.log(':', result.dataTableData);
 * ```
 *
 *   (Input Data Format):
 * ```javascript
 * {
 *   "x": 10,        //  X 
 *   "y": 10         //  Y 
 * }
 * ```
 *
 *   (Output Data Structure):
 * ```javascript
 * {
 *   jsonData: {
 *     gridX: number,        //  X 
 *     gridY: number,        //  Y 
 *     nodes: [              // 
 *       {
 *         x: number,        //  X 
 *         y: number,        //  Y 
 *         value: number,    // 1-5
 *         type: number,     // 
 *         coord: { x: number, y: number }  // 
 *       }
 *     ],
 *     type: 'grid'          // 
 *   },
 *   dashboardData: {
 *     totalNodes: number,   // 
 *     gridSize: string,     // 
 *     gridX: number,        // X 
 *     gridY: number,        // Y 
 *     nodeCount: number     // 
 *   },
 *   dataTableData: [            // 
 *     {
 *       '#': number,        // 
 *       name: string,       // 
 *       gridSize: string,   // 
 *       totalNodes: number, // 
 *       nodes: Array        // 
 *     }
 *   ],
 * }
 * ```
 *
 *   (Data Processing Flow):
 * 1. x, y
 * 2. 
 * 3. 
 * 4. 
 * 5. 
 * 6. 
 * 7. 
 *
 *   (Important Notes):
 * - 10x10
 * - 
 * - 
 * - 
 *
 * @param {Object} jsonData -  JSON 
 * @param {number} [jsonData.x=10] -  X 
 * @param {number} [jsonData.y=10] -  Y 
 * @returns {Object} - 
 *
 * @example
 * // 
 * const jsonData = { x: 3, y: 3 };
 * const result = await processGridSchematicJson(jsonData);
 *
 * @since 1.0.0
 * @see {@link loadGridSchematicJson} 
 */
async function processGridSchematicJson(jsonData) {
  console.log(' :', jsonData);

  // 
  const gridX = parseInt(jsonData.x) || 10;
  const gridY = parseInt(jsonData.y) || 10;

  console.log(` : ${gridX} x ${gridY}`);

  // 
  const gridNodes = [];
  for (let y = 0; y < gridY; y++) {
    for (let x = 0; x < gridX; x++) {
      gridNodes.push({
        x: x,
        y: y,
        value: Math.floor(Math.random() * 5) + 1, //  1-5 
        type: 1, // 
        coord: { x: x, y: y },
      });
    }
  }

  // 
  const dashboardData = {
    totalNodes: gridX * gridY,
    gridSize: `${gridX} x ${gridY}`,
    gridX: gridX,
    gridY: gridY,
    nodeCount: gridNodes.length,
  };

  // 
  const layerInfoData = {
    totalNodes: gridX * gridY,
    gridSize: `${gridX} x ${gridY}`,
    gridX: gridX,
    gridY: gridY,
  };

  // 
  const dataTableData = [
    {
      '#': 1,
      name: ` (${gridX}x${gridY})`,
      gridSize: `${gridX} x ${gridY}`,
      totalNodes: gridX * gridY,
      nodes: gridNodes,
    },
  ];

  return {
    jsonData: jsonData, // 
    processedJsonData: {
      gridX: gridX,
      gridY: gridY,
      nodes: gridNodes,
      type: 'grid',
    },
    dashboardData,
    dataTableData,
    layerInfoData,
  };
}

/**
 *   JSON  (Process Data Layer JSON Data)
 *
 *  JSON 
 * 
 * 
 *
 *   (Main Features):
 * - 
 * - 
 * - 
 * - 
 * - 
 * - 
 *
 *   (Supported Data Formats):
 * - 
 *   - Array<{ nodes: Array, name: string, color: string }>
 *   - 
 * -  JSON 
 *   - Array<{ name: string, id: string, []: any }>
 *   - 
 *
 *   (Data Processing Logic):
 * 1. 
 * 2. 
 * 3. 
 * 4. 
 * 5. 
 *
 *   (Usage Examples):
 * ```javascript
 * // 
 * const schematicData = [
 *   {
 *     name: 'Line 1',
 *     color: 'red',
 *     nodes: [
 *       { coord: { x: 0, y: 0 }, value: 0 },
 *       { coord: { x: 1, y: 0 }, value: 0 }
 *     ]
 *   }
 * ];
 * const result = await processDataLayerJson(schematicData);
 *
 * // 
 * const geoData = [
 *   { name: 'Station 1', id: 'S1', type: 'metro' },
 *   { name: 'Station 2', id: 'S2', type: 'metro' }
 * ];
 * const result = await processDataLayerJson(geoData);
 * ```
 *
 *   (Schematic Node Data Format):
 * ```javascript
 * [
 *   {
 *     name: string,           // 
 *     color: string,          // 
 *     nodes: [                // 
 *       {
 *         coord: { x: number, y: number },  // 
 *         value: number,       // 
 *         []: any      // 
 *       }
 *     ]
 *   }
 * ]
 * ```
 *
 *   (Standard Geo Data Format):
 * ```javascript
 * [
 *   {
 *     name: string,           // 
 *     id: string,             //  ID
 *     []: any         // 
 *   }
 * ]
 * ```
 *
 *   (Output Data Structure):
 * ```javascript
 * {
 *   jsonData: Object | null,  //  JSON  null
 *   dashboardData: {            // 
 *     totalLines?: number,    // 
 *     totalNodes?: number,    // 
 *     lineNames?: string[],   // 
 *     totalCount?: number,    // 
 *     itemNames?: string[]    // 
 *   },
 *   dataTableData: Array          // 
 * }
 * ```
 *
 *   (Data Processing Flow):
 * 1. 
 * 2. 
 * 3. 
 * 4. 
 * 5. 
 * 6. 
 *
 *   (Important Notes):
 * - 
 * - 
 * - 
 * - 
 *
 * @param {Object} jsonData -  JSON 
 * @param {Array} [jsonData] - 
 * @param {Array} [jsonData] - 
 * @returns {Object} - 
 *
 * @example
 * // 
 * const schematicData = [{ name: 'Line 1', nodes: [] }];
 * const result = await processDataLayerJson(schematicData);
 *
 * // 
 * const geoData = [{ name: 'Station 1', id: 'S1' }];
 * const result = await processDataLayerJson(geoData);
 *
 * @since 1.0.0
 * @see {@link randomizeNodeValues} 
 * @see {@link loadDataLayerJson} 
 */
async function processDataLayerJson(jsonData) {
  // 
  if (Array.isArray(jsonData) && jsonData.length > 0 && jsonData[0].nodes) {
    // 
    console.log(' ', jsonData.length, '');

    //  1-5 
    const processedJsonData = jsonData.map((line) => ({
      ...line,
      nodes: randomizeNodeValues(line.nodes),
    }));

    // 
    const dashboardData = {
      totalLines: processedJsonData.length,
      totalNodes: processedJsonData.reduce((sum, line) => sum + line.nodes.length, 0),
      lineNames: processedJsonData.map((line) => line.name),
    };

    // 
    const layerInfoData = {
      totalLines: processedJsonData.length,
      totalNodes: processedJsonData.reduce((sum, line) => sum + line.nodes.length, 0),
      lineNames: processedJsonData.map((line) => line.name),
    };

    //  dataTableData
    const dataTableData = processedJsonData.map((line, index) => ({
      '#': index + 1,
      color: line.color,
      name: line.name,
      nodes: line.nodes,
    }));

    return {
      jsonData: jsonData, // 
      processedJsonData: processedJsonData, // 
      dashboardData,
      dataTableData,
      layerInfoData,
    };
  }

  //  JSON  - 
  console.log('  JSON ', jsonData.length, '');

  // 
  const dashboardData = {
    totalCount: jsonData.length,
    itemNames: jsonData.map((item) => item.name || item.id || ''),
  };

  // 
  const layerInfoData = {
    totalItems: jsonData.length,
    itemNames: jsonData.map((item) => item.name || item.id || ''),
    hasFeatures: jsonData.some((item) => item.features),
    hasProperties: jsonData.some((item) => item.properties),
  };

  return {
    jsonData: jsonData, // 
    processedJsonData: jsonData, // 
    dashboardData,
    layerInfoData,
    dataTableData: jsonData.map((item, index) => ({
      '#': index + 1,
      name: item.name || item.id || '',
      ...item,
    })),
  };
}
