<script setup>
  /**
   *  D3jsTab.vue - D3.js 
   *
   * 
   * 1.   - 
   * 2.   - 
   * 3.   - 
   * 4.  D3.js  -  D3.js 
   * 5.   - 
   *
   * @component D3jsTab
   * @version 2.0.0
   * @author Kevin Cheng
   */

  import { ref, computed, watch, onMounted, onUnmounted, nextTick } from 'vue';
  import { useDataStore } from '@/stores/dataStore.js';
  import * as d3 from 'd3';

  // Props
  const props = defineProps({
    containerHeight: {
      type: Number,
      default: 600,
    },
    isPanelDragging: {
      type: Boolean,
      default: false,
    },
    activeMarkers: {
      type: Array,
      default: () => [],
    },
  });

  const dataStore = useDataStore();

  const activeLayerTab = ref(null); /**   */

  // ====================   (Schematic Drawing State) ====================

  /**   (Grid Data State) */
  const gridData = ref(null);
  const gridDimensions = ref({ x: 10, y: 10 });

  /**   (Administrative District Data State) */
  const nodeData = ref(null);
  const linkData = ref(null);

  // ====================   (Visualization Constants) ====================

  /**   (Color Configuration) */
  const COLOR_CONFIG = {
    BACKGROUND: '#212121',
    GRID_LINE: '#666666',
    GRID_LINE_SECONDARY: '#333333',
    NODE_FILL: '#4CAF50',
    NODE_STROKE: '#2E7D32',
    TEXT_FILL: '#FFFFFF',
  };

  /**   (Color Mapping) */
  const colorMap = {
    red: '#ff0000',
    lightpink: '#ffb3ba',
    blue: '#0066cc',
    green: '#00aa44',
    lightgreen: '#90ee90',
    orange: '#ff8800',
    brown: '#8b4513',
    yellow: '#ffcc00',
    purple: '#800080',
    paleturquoise: '#afeeee',
    limegreen: '#32cd32',
  };

  // ResizeObserver 
  let resizeObserver = null;

  // 
  const visibleLayers = computed(() => {
    const allLayers = dataStore.getAllLayers();
    return allLayers.filter((layer) => layer.visible);
  });

  /**
   *   (Set Active Layer Tab)
   * @param {string} layerId -  ID
   */
  const setActiveLayerTab = (layerId) => {
    console.log(' :', activeLayerTab.value, '->', layerId);

    // 
    if (activeLayerTab.value === layerId) {
      console.log(' ');
      return;
    }

    //  SVG 
    d3.select('#schematic-container').selectAll('svg').remove();
    console.log('  SVG ');

    // 
    gridData.value = null;
    nodeData.value = null;
    linkData.value = null;
    console.log(' ');

    // 
    activeLayerTab.value = layerId;
    console.log(' :', layerId);
  };

  /**
   *   (Current Layer Summary)
   */
  const currentLayerSummary = computed(() => {
    console.log('currentLayerSummary computed - activeLayerTab:', activeLayerTab.value);
    console.log('currentLayerSummary computed - visibleLayers:', visibleLayers.value);

    if (!activeLayerTab.value) {
      console.log('currentLayerSummary - no activeLayerTab, returning null');
      return null;
    }

    const layer = visibleLayers.value.find((l) => l.layerId === activeLayerTab.value);
    console.log('currentLayerSummary - found layer:', layer);

    const result = layer ? layer.dashboardData || null : null;
    console.log('currentLayerSummary - returning:', result);
    return result;
  });

  /**
   *   () (Get Layer Full Title with Group Name)
   */
  const getLayerFullTitle = (layer) => {
    if (!layer) return { groupName: null, layerName: '' };
    const groupName = dataStore.findGroupNameByLayerId(layer.layerId);
    return {
      groupName: groupName,
      layerName: layer.layerName,
    };
  };

  /**
   *   (Check if Layer is Grid Schematic)
   * @param {string} layerId -  ID
   * @returns {boolean} 
   */
  const isGridSchematicLayer = (layerId) => {
    if (!layerId) return false;
    const layer = dataStore.findLayerById(layerId);
    return layer && layer.isGridSchematic === true;
  };

  // ====================   (Data Loading and Processing Functions) ====================

  /**
   *   1-5  (Randomize Node Values)
   * @param {Array} nodes - 
   * @returns {Array} - 
   */
  const randomizeNodeValues = (nodes) => {
    console.log(' :', nodes.length);
    const randomizedNodes = nodes.map((node) => {
      const newValue = Math.floor(Math.random() * 5) + 1; //  1-5 
      console.log(`  ${node.coord?.x},${node.coord?.y}  ${node.value}  ${newValue}`);
      return {
        ...node,
        value: newValue,
      };
    });
    console.log(' 3:', randomizedNodes.slice(0, 3));
    return randomizedNodes;
  };

  /**
   *   (Load Layer Data)
   * @param {string} layerId -  ID
   */
  const loadLayerData = async (layerId) => {
    try {
      // 
      const targetLayer = dataStore.findLayerById(layerId);
      if (!targetLayer) {
        throw new Error(`: ${layerId}`);
      }

      console.log(' :', targetLayer.jsonFileName);

      //  jsonLoader 
      const result = await targetLayer.jsonLoader(targetLayer);

      // 
      if (result.processedJsonData) {
        if (result.processedJsonData.type === 'grid') {
          // 
          gridData.value = result.processedJsonData;
          gridDimensions.value = {
            x: result.processedJsonData.gridX,
            y: result.processedJsonData.gridY,
          };
          console.log(' :', gridData.value);
        } else {
          // 
          nodeData.value = result.processedJsonData;
          console.log(' :', nodeData.value);
          setLinkData();
        }
      } else if (result.dataTableData && result.dataTableData.length > 0) {
        // 
        const schematicData = result.dataTableData.map((item) => ({
          color: item.color,
          name: item.name,
          nodes: item.nodes || [],
        }));

        //  1-5 
        const processedData = schematicData.map((line) => ({
          ...line,
          nodes: randomizeNodeValues(line.nodes),
        }));

        nodeData.value = processedData;
        console.log(' :', nodeData.value);
        setLinkData();
      } else {
        throw new Error('');
      }
    } catch (error) {
      console.error(' :', error.message);
    }
  };

  /**
   *   (Set Link Data)
   */
  const setLinkData = () => {
    if (!nodeData.value) return;

    linkData.value = [];

    nodeData.value.forEach((path) => {
      let thisX, thisY;
      let nodes = [];

      path.nodes.slice(0, path.nodes.length - 1).forEach((node) => {
        thisX = node.coord.x;
        thisY = node.coord.y;

        switch (node.type) {
          case 1:
          case 6:
          case 21:
          case 41:
            thisX = node.coord.x + 0.5;
            thisY = node.coord.y;
            break;
          case 2:
          case 8:
          case 12:
          case 32:
            thisX = node.coord.x;
            thisY = node.coord.y - 0.5;
            break;
          case 3:
          case 5:
          case 23:
          case 43:
            thisX = node.coord.x - 0.5;
            thisY = node.coord.y;
            break;
          case 4:
          case 7:
          case 14:
          case 34:
            thisX = node.coord.x;
            thisY = node.coord.y + 0.5;
            break;
        }

        nodes.push({
          value: node.value,
          type: node.type,
          coord: { x: thisX, y: thisY },
        });
      });

      let data = {
        color: colorMap[path.color] || path.color,
        name: path.name,
        nodes: nodes,
      };

      linkData.value.push(data);
    });

    console.log('linkData', linkData.value);
  };

  // ====================   (Container Dimensions and Drawing Functions) ====================

  /**
   *   (Get Container Dimensions)
   * @returns {Object}  width  height 
   */
  const getDimensions = () => {
    const container = document.getElementById('schematic-container');
    if (container) {
      // 
      const rect = container.getBoundingClientRect();
      const width = container.clientWidth || rect.width;
      const height = container.clientHeight || rect.height;

      console.log(' :', {
        width,
        height,
        clientWidth: container.clientWidth,
        clientHeight: container.clientHeight,
        rectWidth: rect.width,
        rectHeight: rect.height,
      });

      const dimensions = {
        width: Math.max(width, 400),
        height: Math.max(height, 300),
      };

      //  dataStore 
      dataStore.updateD3jsDimensions(dimensions.width, dimensions.height);

      return dimensions;
    }
    // 
    const defaultDimensions = {
      width: 800,
      height: 600,
    };

    //  dataStore 
    dataStore.updateD3jsDimensions(defaultDimensions.width, defaultDimensions.height);

    return defaultDimensions;
  };

  /**
   *   (Draw Grid Schematic)
   */
  const drawGridSchematic = () => {
    if (!gridData.value) return;

    // 
    const dimensions = getDimensions();

    // 
    const margin = { top: 20, right: 20, bottom: 20, left: 20 };
    const width = dimensions.width - margin.left - margin.right;
    const height = dimensions.height - margin.top - margin.bottom;

    //  SVG
    const existingSvg = d3.select('#schematic-container').select('svg');
    if (existingSvg.size() > 0) {
      const existingWidth = parseFloat(existingSvg.attr('width'));
      const existingHeight = parseFloat(existingSvg.attr('height'));

      //  2px
      // 
      if (
        Math.abs(existingWidth - (width + margin.left + margin.right)) < 2 &&
        Math.abs(existingHeight - (height + margin.top + margin.bottom)) < 2
      ) {
        console.log(' ');
        return;
      }
    }

    // 
    d3.select('#schematic-container').selectAll('svg').remove();

    //  SVG 
    const svg = d3
      .select('#schematic-container')
      .append('svg')
      .attr('width', width + margin.left + margin.right)
      .attr('height', height + margin.top + margin.bottom)
      .style('background-color', COLOR_CONFIG.BACKGROUND)
      .style('transition', 'all 0.2s ease-in-out');

    // 
    const cellWidth = width / gridDimensions.value.x;
    const cellHeight = height / gridDimensions.value.y;

    console.log(' :', { cellWidth, cellHeight });

    // 
    drawGridLines(svg, width, height, cellWidth, cellHeight, margin);

    // 
    drawGridNodes(svg, cellWidth, cellHeight, margin);
  };

  /**
   *   (Draw Grid Lines)
   * @param {Object} svg - D3 SVG 
   * @param {number} width - 
   * @param {number} height - 
   * @param {number} cellWidth - 
   * @param {number} cellHeight - 
   * @param {Object} margin - 
   */
  const drawGridLines = (svg, width, height, cellWidth, cellHeight, margin) => {
    // 
    for (let i = 0; i <= gridDimensions.value.x; i++) {
      svg
        .append('line')
        .style('stroke', COLOR_CONFIG.GRID_LINE)
        .style('stroke-width', 1)
        .attr('x1', margin.left + i * cellWidth)
        .attr('y1', margin.top)
        .attr('x2', margin.left + i * cellWidth)
        .attr('y2', margin.top + height);
    }

    // 
    for (let i = 0; i <= gridDimensions.value.y; i++) {
      svg
        .append('line')
        .style('stroke', COLOR_CONFIG.GRID_LINE)
        .style('stroke-width', 1)
        .attr('x1', margin.left)
        .attr('y1', margin.top + i * cellHeight)
        .attr('x2', margin.left + width)
        .attr('y2', margin.top + i * cellHeight);
    }

    // 
    for (let i = 0; i < gridDimensions.value.x; i++) {
      svg
        .append('line')
        .style('stroke', COLOR_CONFIG.GRID_LINE_SECONDARY)
        .style('stroke-width', 0.5)
        .attr('x1', margin.left + (i + 0.5) * cellWidth)
        .attr('y1', margin.top)
        .attr('x2', margin.left + (i + 0.5) * cellWidth)
        .attr('y2', margin.top + height);
    }

    for (let i = 0; i < gridDimensions.value.y; i++) {
      svg
        .append('line')
        .style('stroke', COLOR_CONFIG.GRID_LINE_SECONDARY)
        .style('stroke-width', 0.5)
        .attr('x1', margin.left)
        .attr('y1', margin.top + (i + 0.5) * cellHeight)
        .attr('x2', margin.left + width)
        .attr('y2', margin.top + (i + 0.5) * cellHeight);
    }
  };

  /**
   *   (Draw Grid Nodes)
   * @param {Object} svg - D3 SVG 
   * @param {number} cellWidth - 
   * @param {number} cellHeight - 
   * @param {Object} margin - 
   */
  const drawGridNodes = (svg, cellWidth, cellHeight, margin) => {
    if (!gridData.value || !gridData.value.nodes) return;

    // 
    const nodeGroup = svg.append('g').attr('class', 'grid-nodes');

    // 
    gridData.value.nodes.forEach((node) => {
      const x = margin.left + (node.x + 0.5) * cellWidth;
      const y = margin.top + (node.y + 0.5) * cellHeight;

      // 
      nodeGroup
        .append('circle')
        .attr('cx', x)
        .attr('cy', y)
        .attr('r', Math.min(cellWidth, cellHeight) * 0.3)
        .style('fill', COLOR_CONFIG.NODE_FILL)
        .style('stroke', COLOR_CONFIG.NODE_STROKE)
        .style('stroke-width', 2)
        .style('opacity', 0.8)
        .on('mouseover', function () {
          d3.select(this).style('opacity', 1).style('stroke-width', 3);
        })
        .on('mouseout', function () {
          d3.select(this).style('opacity', 0.8).style('stroke-width', 2);
        });

      // 
      nodeGroup
        .append('text')
        .attr('x', x)
        .attr('y', y)
        .attr('text-anchor', 'middle')
        .attr('dominant-baseline', 'middle')
        .attr('font-size', Math.min(cellWidth, cellHeight) * 0.2)
        .attr('font-weight', 'bold')
        .attr('fill', COLOR_CONFIG.TEXT_FILL)
        .text(node.value);
    });
  };

  /**
   *   (Draw Administrative District Schematic)
   */
  const drawAdministrativeSchematic = () => {
    if (!nodeData.value) return;

    // px
    let dimensions = getDimensions();

    // 
    const margin = { top: 20, right: 20, bottom: 20, left: 20 };
    const width = dimensions.width - margin.left - margin.right;
    const height = dimensions.height - margin.top - margin.bottom;

    // 
    const allPoints = nodeData.value.flatMap((d) =>
      d.nodes.map((node) => ({
        x: node.coord.x,
        y: node.coord.y,
      }))
    );

    console.log('allPoints', allPoints);

    // 
    let xMax = d3.max(allPoints, (d) => d.x);
    let yMax = d3.max(allPoints, (d) => d.y);

    console.log('Maximum x: ', xMax);
    console.log('Maximum y: ', yMax);

    //  SVG
    const existingSvg = d3.select('#schematic-container').select('svg');
    if (existingSvg.size() > 0) {
      const existingWidth = parseFloat(existingSvg.attr('width'));
      const existingHeight = parseFloat(existingSvg.attr('height'));

      //  2px
      // 
      if (
        Math.abs(existingWidth - (width + margin.left + margin.right)) < 2 &&
        Math.abs(existingHeight - (height + margin.top + margin.bottom)) < 2
      ) {
        console.log(' ');
        return;
      }
    }

    // 
    d3.select('#schematic-container').selectAll('svg').remove();

    //  SVG 
    const svg = d3
      .select('#schematic-container')
      .append('svg')
      .attr('width', width + margin.left + margin.right)
      .attr('height', height + margin.top + margin.bottom)
      .style('background-color', COLOR_CONFIG.BACKGROUND)
      .style('transition', 'all 0.2s ease-in-out'); // 

    // 
    const actualWidth = width;
    const actualHeight = height;

    // 

    // 
    const x = d3
      .scaleLinear()
      .domain([0, xMax])
      .range([margin.left, margin.left + actualWidth]);
    const y = d3
      .scaleLinear()
      .domain([yMax, 0])
      .range([margin.top, margin.top + actualHeight]);

    // 
    for (let i = 0; i <= xMax; i++) {
      svg
        .append('line')
        .style('stroke', COLOR_CONFIG.GRID_LINE)
        .attr('x1', x(i))
        .attr('y1', margin.top)
        .attr('x2', x(i))
        .attr('y2', margin.top + actualHeight);
    }

    for (let i = 0; i <= yMax; i++) {
      svg
        .append('line')
        .style('stroke', COLOR_CONFIG.GRID_LINE)
        .attr('x1', margin.left)
        .attr('y1', y(i))
        .attr('x2', margin.left + actualWidth)
        .attr('y2', y(i));
    }

    // 
    for (let i = 0; i < xMax; i++) {
      svg
        .append('line')
        .style('stroke', COLOR_CONFIG.GRID_LINE_SECONDARY)
        .attr('x1', (x(i) + x(i + 1)) / 2)
        .attr('y1', margin.top)
        .attr('x2', (x(i) + x(i + 1)) / 2)
        .attr('y2', margin.top + actualHeight);
    }

    for (let i = 0; i < yMax; i++) {
      svg
        .append('line')
        .style('stroke', COLOR_CONFIG.GRID_LINE_SECONDARY)
        .attr('x1', margin.left)
        .attr('y1', (y(i) + y(i + 1)) / 2)
        .attr('x2', margin.left + actualWidth)
        .attr('y2', (y(i) + y(i + 1)) / 2);
    }

    // 
    const lineGenerator = d3
      .line()
      .x((d) => x(d.x))
      .y((d) => y(d.y))
      .curve(d3.curveNatural);

    // 
    nodeData.value.forEach((path) => {
      path.nodes.forEach((node) => {
        let dString = '';
        let nodes = [];

        console.log('node.coord.type', node.type);

        switch (node.type) {
          case 1:
            nodes = [
              { x: node.coord.x - 0.5, y: node.coord.y },
              { x: node.coord.x + 0.5, y: node.coord.y },
            ];
            dString = lineGenerator(nodes);
            break;
          case 2:
            nodes = [
              { x: node.coord.x, y: node.coord.y - 0.5 },
              { x: node.coord.x, y: node.coord.y + 0.5 },
            ];
            dString = lineGenerator(nodes);
            break;
          case 3:
            nodes = [
              { x: node.coord.x + 0.5, y: node.coord.y },
              { x: node.coord.x - 0.5, y: node.coord.y },
            ];
            dString = lineGenerator(nodes);
            break;
          case 4:
            nodes = [
              { x: node.coord.x, y: node.coord.y + 0.5 },
              { x: node.coord.x, y: node.coord.y - 0.5 },
            ];
            dString = lineGenerator(nodes);
            break;
          case 5:
            nodes = [
              { x: node.coord.x, y: node.coord.y },
              { x: node.coord.x - 0.5, y: node.coord.y },
            ];
            dString = lineGenerator(nodes);
            break;
          case 6:
            nodes = [
              { x: node.coord.x + 0.5, y: node.coord.y },
              { x: node.coord.x, y: node.coord.y },
            ];
            dString = lineGenerator(nodes);
            break;
          case 7:
            nodes = [
              { x: node.coord.x, y: node.coord.y + 0.5 },
              { x: node.coord.x, y: node.coord.y },
            ];
            dString = lineGenerator(nodes);
            break;
          case 8:
            nodes = [
              { x: node.coord.x, y: node.coord.y },
              { x: node.coord.x, y: node.coord.y - 0.5 },
            ];
            dString = lineGenerator(nodes);
            break;
          case 12:
          case 43: {
            let xWidth = Math.abs(x(node.coord.x - 0.5) - x(node.coord.x));
            let yHeight = Math.abs(y(node.coord.y) - y(node.coord.y - 0.5));

            let arcWidth = 0;

            if (xWidth < yHeight) {
              arcWidth = xWidth;

              nodes = [
                { x: node.coord.x, y: y.invert(y(node.coord.y) + arcWidth) },
                { x: node.coord.x, y: node.coord.y - 0.5 },
              ];
            } else {
              arcWidth = yHeight;

              nodes = [
                { x: node.coord.x - 0.5, y: node.coord.y },
                { x: x.invert(x(node.coord.x) - arcWidth), y: node.coord.y },
              ];
            }

            console.log('xWidth yHeight arcWidth', xWidth, yHeight, arcWidth);

            console.log('nodes', nodes);

            dString = lineGenerator(nodes);

            console.log('dString', dString);

            const arc = d3
              .arc()
              .innerRadius(arcWidth - 3)
              .outerRadius(arcWidth + 3)
              .startAngle(0)
              .endAngle(Math.PI / 2);

            svg
              .append('path')
              .attr('d', arc)
              .attr(
                'transform',
                `translate(${x(node.coord.x) - arcWidth}, ${y(node.coord.y) + arcWidth})`
              )
              .attr('fill', path.color);
            break;
          }
          case 21:
          case 34: {
            let xWidth = Math.abs(x(node.coord.x - 0.5) - x(node.coord.x));
            let yHeight = Math.abs(y(node.coord.y) - y(node.coord.y - 0.5));

            let arcWidth = 0;

            if (xWidth < yHeight) {
              arcWidth = xWidth;

              nodes = [
                { x: node.coord.x, y: y.invert(y(node.coord.y) - arcWidth) },
                { x: node.coord.x, y: node.coord.y + 0.5 },
              ];
            } else {
              arcWidth = yHeight;

              nodes = [
                { x: node.coord.x + 0.5, y: node.coord.y },
                { x: x.invert(x(node.coord.x) + arcWidth), y: node.coord.y },
              ];
            }

            console.log('xWidth yHeight arcWidth', xWidth, yHeight, arcWidth);

            console.log('nodes', nodes);

            dString = lineGenerator(nodes);

            console.log('dString', dString);

            const arc = d3
              .arc()
              .innerRadius(arcWidth - 3)
              .outerRadius(arcWidth + 3)
              .startAngle(-Math.PI / 2)
              .endAngle(-Math.PI);

            svg
              .append('path')
              .attr('d', arc)
              .attr(
                'transform',
                `translate(${x(node.coord.x) + arcWidth}, ${y(node.coord.y) - arcWidth})`
              )
              .attr('fill', path.color);
            break;
          }
          case 14:
          case 23: {
            let xWidth = Math.abs(x(node.coord.x - 0.5) - x(node.coord.x));
            let yHeight = Math.abs(y(node.coord.y) - y(node.coord.y - 0.5));

            let arcWidth = 0;

            if (xWidth < yHeight) {
              arcWidth = xWidth;

              nodes = [
                { x: node.coord.x, y: y.invert(y(node.coord.y) - arcWidth) },
                { x: node.coord.x, y: node.coord.y + 0.5 },
              ];
            } else {
              arcWidth = yHeight;

              nodes = [
                { x: node.coord.x - 0.5, y: node.coord.y },
                { x: x.invert(x(node.coord.x) - arcWidth), y: node.coord.y },
              ];
            }

            console.log('xWidth yHeight arcWidth', xWidth, yHeight, arcWidth);

            console.log('nodes', nodes);

            dString = lineGenerator(nodes);

            console.log('dString', dString);

            const arc = d3
              .arc()
              .innerRadius(arcWidth - 3)
              .outerRadius(arcWidth + 3)
              .startAngle(Math.PI / 2)
              .endAngle(Math.PI);

            svg
              .append('path')
              .attr('d', arc)
              .attr(
                'transform',
                `translate(${x(node.coord.x) - arcWidth}, ${y(node.coord.y) - arcWidth})`
              )
              .attr('fill', path.color);
            break;
          }
          case 32:
          case 41: {
            let xWidth = Math.abs(x(node.coord.x - 0.5) - x(node.coord.x));
            let yHeight = Math.abs(y(node.coord.y) - y(node.coord.y - 0.5));

            let arcWidth = 0;

            if (xWidth < yHeight) {
              arcWidth = xWidth;

              nodes = [
                { x: node.coord.x, y: y.invert(y(node.coord.y) + arcWidth) },
                { x: node.coord.x, y: node.coord.y - 0.5 },
              ];
            } else {
              arcWidth = yHeight;

              nodes = [
                { x: node.coord.x + 0.5, y: node.coord.y },
                { x: x.invert(x(node.coord.x) + arcWidth), y: node.coord.y },
              ];
            }

            console.log('xWidth yHeight arcWidth', xWidth, yHeight, arcWidth);

            console.log('nodes', nodes);

            dString = lineGenerator(nodes);

            console.log('dString', dString);

            const arc = d3
              .arc()
              .innerRadius(arcWidth - 3)
              .outerRadius(arcWidth + 3)
              .startAngle(0)
              .endAngle(-Math.PI / 2);

            svg
              .append('path')
              .attr('d', arc)
              .attr(
                'transform',
                `translate(${x(node.coord.x) + arcWidth}, ${y(node.coord.y) + arcWidth})`
              )
              .attr('fill', path.color);
            break;
          }
          default:
            break;
        }

        if (dString !== '') {
          svg
            .append('path')
            .attr('d', dString)
            .attr('stroke', path.color)
            .attr('fill', 'none')
            .attr('stroke-width', 6);
        }
      });
    });

    // 
    if (linkData.value) {
      const allLinks = linkData.value.flatMap((line) =>
        line.nodes.map((node) => ({
          ...node,
        }))
      );

      console.log('allLinks', allLinks);

      allLinks.forEach((node) => {
        svg
          .append('text')
          .attr('x', x(node.coord.x))
          .attr('y', y(node.coord.y))
          .attr('text-anchor', 'middle')
          .attr('dominant-baseline', 'middle')
          .attr('font-size', '10px')
          .attr('fill', 'white')
          .text(`${node.value}`);
      });
    }
  };

  /**
   *   (Unified Drawing Function)
   * 
   */
  const drawSchematic = () => {
    if (isGridSchematicLayer(activeLayerTab.value)) {
      drawGridSchematic();
    } else {
      drawAdministrativeSchematic();
    }
  };

  /**
   *   (Resize)
   * 
   */
  const resize = () => {
    console.log(' D3jsTab:  resize');
    // 
    getDimensions();
    drawSchematic();
  };

  // 
  const previousLayers = ref([]);

  /**
   *  
   */
  watch(
    () => visibleLayers.value,
    (newLayers) => {
      // 
      if (newLayers.length === 0) {
        activeLayerTab.value = null;
        previousLayers.value = [];
        return;
      }

      // 
      const previousLayerIds = previousLayers.value.map((layer) => layer.layerId);
      const newLayerIds = newLayers.map((layer) => layer.layerId);
      const addedLayerIds = newLayerIds.filter((id) => !previousLayerIds.includes(id));

      // 
      if (addedLayerIds.length > 0) {
        const newestAddedLayerId = addedLayerIds[addedLayerIds.length - 1];
        activeLayerTab.value = newestAddedLayerId;
      }
      // 
      else if (
        !activeLayerTab.value ||
        !newLayers.find((layer) => layer.layerId === activeLayerTab.value)
      ) {
        activeLayerTab.value = newLayers[0].layerId;
      }

      // 
      previousLayers.value = [...newLayers];
    },
    { deep: true, immediate: true }
  );

  /**
   *  
   */
  watch(
    () => activeLayerTab.value,
    async (newLayerId, oldLayerId) => {
      if (newLayerId && newLayerId !== oldLayerId) {
        console.log(' :', oldLayerId, '->', newLayerId);

        //  SVG 
        d3.select('#schematic-container').selectAll('svg').remove();
        console.log('  SVG ');

        // 
        gridData.value = null;
        nodeData.value = null;
        linkData.value = null;
        console.log(' ');

        // 
        console.log(' :', newLayerId);
        await loadLayerData(newLayerId);

        //  DOM 
        await nextTick();
        console.log(' ');
        drawSchematic();
        console.log(' ');
      }
    }
  );

  /**
   *  
   */
  watch(
    () => props.containerHeight,
    () => {
      // 
      nextTick(() => {
        resize();
      });
    }
  );

  /**
   *   (Component Mounted Event)
   */
  onMounted(async () => {
    console.log('D3jsTab mounted - visibleLayers:', visibleLayers.value);
    console.log('D3jsTab mounted - activeLayerTab:', activeLayerTab.value);

    // 
    if (visibleLayers.value.length > 0 && !activeLayerTab.value) {
      activeLayerTab.value = visibleLayers.value[0].layerId;
      console.log('D3jsTab - Set initial activeLayerTab to:', activeLayerTab.value);

      // 
      await loadLayerData(activeLayerTab.value);
      await nextTick();
      drawSchematic();
    }

    // 
    window.addEventListener('resize', resize);

    // 
    const container = document.getElementById('schematic-container');
    if (container && window.ResizeObserver) {
      resizeObserver = new ResizeObserver((entries) => {
        for (let entry of entries) {
          const { width, height } = entry.contentRect;
          if (width > 0 && height > 0) {
            resize();
          }
        }
      });
      resizeObserver.observe(container);

      // 
      const parentContainer = container.parentElement;
      if (parentContainer) {
        resizeObserver.observe(parentContainer);
      }
    }
  });

  /**
   *   (Component Unmounted Event)
   */
  onUnmounted(() => {
    window.removeEventListener('resize', resize);

    //  ResizeObserver
    if (resizeObserver) {
      resizeObserver.disconnect();
      resizeObserver = null;
    }
  });

  // 
  defineExpose({
    resize, // 
  });
</script>

<template>
  <!--   D3.js  -->
  <div class="d-flex flex-column my-bgcolor-gray-200 h-100">
    <!--   -->
    <div v-if="visibleLayers.length > 0" class="">
      <ul class="nav nav-tabs nav-fill">
        <li
          v-for="layer in visibleLayers"
          :key="layer.layerId"
          class="nav-item d-flex flex-column align-items-center"
        >
          <!-- tab -->
          <div
            class="btn nav-link rounded-0 border-0 position-relative d-flex align-items-center justify-content-center my-bgcolor-gray-200"
            :class="{
              active: activeLayerTab === layer.layerId,
            }"
            @click="setActiveLayerTab(layer.layerId)"
          >
            <span>
              <span v-if="getLayerFullTitle(layer).groupName" class="my-title-xs-gray"
                >{{ getLayerFullTitle(layer).groupName }} -
              </span>
              <span class="my-title-sm-black">{{ getLayerFullTitle(layer).layerName }}</span>
            </span>
          </div>
          <div class="w-100" :class="`my-bgcolor-${layer.colorName}`" style="min-height: 4px"></div>
        </li>
      </ul>
    </div>

    <!--  -->
    <div v-if="visibleLayers.length > 0" class="flex-grow-1 d-flex flex-column my-bgcolor-white">
      <!--   -->
      <div v-if="currentLayerSummary" class="flex-grow-1 d-flex flex-column">
        <!-- D3.js  -  -->
        <div class="flex-grow-1 d-flex flex-column">
          <div
            class="flex-grow-1"
            :style="{
              height: Math.max(props.containerHeight - 100, 300) + 'px',
              minHeight: '300px',
            }"
          >
            <!--   (Unified Schematic Container) -->
            <div
              id="schematic-container"
              class="w-100 h-100"
              style="min-height: 300px; overflow: hidden"
            ></div>
          </div>
        </div>
      </div>
      <div v-else class="flex-grow-1 d-flex align-items-center justify-content-center">
        <div class="text-center">
          <div class="my-title-md-gray"></div>
        </div>
      </div>
    </div>

    <!--  -->
    <div v-else class="flex-grow-1 d-flex align-items-center justify-content-center">
      <div class="text-center">
        <div class="my-title-md-gray p-3"></div>
      </div>
    </div>
  </div>
</template>

