/**
 *   (Main Application Entry Point)
 *
 *  (Features):
 * 1.   Bootstrap 5 
 * 2.   Vue 3 
 * 3.   Vue Router 4 
 * 4.   Pinia 
 * 5.   DOM 
 * 6.  Font AwesomeBootstrap
 * 7.  
 *
 *  (Technology Stack):
 * - Vue 3 (Composition API) - 
 * - Vue Router 4 - 
 * - Pinia - Vue 3 
 * - Bootstrap 5 -  UI 
 * - Font Awesome - 
 * - D3.js - 
 *
 *  (Architecture Design):
 * -  Composition API  TypeScript 
 * -  Pinia 
 * - 
 * - 
 *
 *  (Development Environment):
 * - 
 * -  ESLint  Prettier 
 * - 
 *
 * @file main.js
 * @version 2.0.0
 * @author Kevin Cheng
 * @since 1.0.0
 */

// ====================  Vue  (Vue Core Module Imports) ====================

/**
 * Vue 3 
 *  createApp  Vue  Composition API
 */
import { createApp } from 'vue';

/**
 * Pinia 
 * Vue 3  TypeScript 
 */
import { createPinia } from 'pinia';

// ====================   (Application Component Imports) ====================

/**
 * 
 * 
 */
import App from './App.vue';

/**
 * Vue Router 
 * 
 */
import router from './router';

// ====================   (Third-Party Style Files) ====================

/**
 * Bootstrap 5 CSS 
 * UI 
 * 5.3.0
 */
import 'bootstrap/dist/css/bootstrap.min.css';

/**
 * Font Awesome 
 * 
 * 6.7.2
 */
import '@fortawesome/fontawesome-free/css/all.min.css';

// ====================   (Custom Style Files) ====================

/**
 * 
 *  CSS 
 * 
 */
import './assets/css/common.css';

// ====================   JavaScript  (Third-Party JavaScript Files) ====================

/**
 * Bootstrap 5 JavaScript 
 *  Popper.js
 * -  (Dropdown)
 * -  (Modal)
 * -  (Tooltip)
 * -  (Popover)
 * -  (Collapse)
 * -  (Carousel)
 * 5.3.0
 */
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

// ====================   (Development Debug Information) ====================

/**
 * 
 * 
 */
console.log(' ');

// ====================  Vue  (Vue Application Instance Creation) ====================

/**
 *  Vue 3 
 *  createApp  App
 *
 * @type {App} Vue 
 */
const app = createApp(App);

// ====================  Pinia  (Pinia State Management Instance Creation) ====================

/**
 *  Pinia 
 * Pinia  Vue 3  TypeScript 
 *  API 
 *
 * @type {Store} Pinia 
 */
const pinia = createPinia();

// ====================   (Router System Registration) ====================

/**
 *  Vue Router 
 * 
 * - 
 * - 
 * - 
 * - 
 */
app.use(router);

// ====================   (State Management System Registration) ====================

/**
 *  Pinia 
 * 
 * - 
 * - 
 * - 
 * - 
 */
app.use(pinia);

// ====================   (Application Mounting) ====================

/**
 *  Vue  DOM 
 *  index.html  id="app" 
 * 
 *
 * @param {string} '#app' - DOM 
 */
app.mount('#app');

// ====================   (Application Startup Debug Information) ====================

/**
 * 
 * 
 * 
 */
console.log(' Schematic Map');
console.log(' Pinia ');
console.log(' Vue Router ');
console.log(' Bootstrap 5 UI ');
console.log(' Font Awesome ');
