// ====================   (Third-Party Library Imports) ====================

/**
 * Vue Router 4 
 * 
 *
 * @see https://router.vuejs.org/
 */
import { createRouter, createWebHistory } from 'vue-router';

/**
 * 
 * 
 *
 * @see ../views/HomeView.vue
 */
import HomeView from '../views/HomeView.vue';

// ====================   (Route Configuration Definition) ====================

/**
 *   (Routes Configuration Array)
 *
 * 
 * 
 *
 * 
 * - 
 * - 
 * - 
 * - 
 *
 * @type {Array<Object>}
 * @description 
 */
const routes = [
  {
    path: '/', //  
    name: 'Home', // 
    component: HomeView, //  Vue 
    meta: {
      title: '', // 
      description: 'Schematic Map', // 
      requiresAuth: false, // 
    },
  },
  // 
  // {
  //   path: '/about',
  //   name: 'About',
  //   component: () => import('../views/AboutView.vue'),
  //   meta: {
  //     title: '',
  //     description: '',
  //     requiresAuth: false,
  //   },
  // },
];

// ====================   (Router Instance Creation) ====================

/**
 *   (Router Instance Creation)
 *
 *  Vue Router 4 
 *  HTML5 History API  URL 
 *
 * 
 * - history:  HTML5 History API 
 * - base:  GitHub Pages 
 * - routes: 
 * - scrollBehavior: 
 *
 * @type {Router}
 * @description Vue Router 
 */
const router = createRouter({
  /**
   *  HTML5 History API 
   *  URL  # 
   *  index.html
   */
  history: createWebHistory('/schematic-map-rwd/'),

  /**
   * 
   * 
   */
  routes,

  /**
   * 
   * 
   */
  scrollBehavior(to, from, savedPosition) {
    // 
    if (savedPosition) {
      return savedPosition;
    }
    // 
    return { top: 0 };
  },
});

// ====================   (Route Guards Configuration) ====================

/**
 *   (Global Before Guard)
 *
 * 
 * 
 *
 * @param {RouteLocationNormalized} to - 
 * @param {RouteLocationNormalized} from - 
 * @param {Function} next - 
 */
router.beforeEach((to, from, next) => {
  // 
  if (to.meta.title) {
    document.title = `${to.meta.title} - `;
  } else {
    document.title = '';
  }

  // 
  if (process.env.NODE_ENV === 'development') {
    console.log(` : ${from.path}  ${to.path}`);
  }

  // 
  next();
});

/**
 *   (Global After Guard)
 *
 * 
 *  Google Analytics
 *
 * @param {RouteLocationNormalized} to - 
 * @param {RouteLocationNormalized} from - 
 */
router.afterEach((to) => {
  // 
  if (process.env.NODE_ENV === 'development') {
    console.log(` : ${to.path}`);
  }

  // 
  // Google Analytics 
  // gtag('config', 'GA_MEASUREMENT_ID', {
  //   page_path: to.fullPath,
  // });
});

// ====================   (Module Export) ====================

/**
 * 
 *  main.js  Vue 
 */
export default router;
